













export default {
	data: {
		students: [],
		table: '<view class="list">某某某-医疗-电话：18313023631<view class="list1"  @longtap="getTel()">拨打电话</view><view class="list1">查看位置</view></view>'
	},
	onLoad() {
		uni.request({
			url: 'http://148.70.148.17:8080/sos/sosinfor',
			// url: 'http://192.168.191.1:8080/user/infor',
			success: (res) => {
				this.students = res.data;
				uni.stopPullDownRefresh();
			},
			fail: (res) => {
				uni.showToast({
					icon: 'none',
					title: '网络服务异常！'
				});
				uni.stopPullDownRefresh();
			}
		});
	},
	methods: {
		logout() {
			uni.showModal({
				title: '提示',
				content: '确认退出登陆？',
				success: function(res) {
					if (res.confirm) {
						uni.clearStorageSync();
						uni.reLaunch({
							url: '../login/login'
						});
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
		getTel(tel) {
			uni.makePhoneCall({
				phoneNumber: tel
			});
		},
		openLocation(longitude, latitude) {
			var longitude1 = longitude;
			var latitude1 = latitude;
			uni.openLocation({
				latitude: latitude1,
				longitude: longitude1,
				success: function() {
					console.log('success');
				},
				fail(res) {
					uni.showToast({
						icon: 'none',
						title: '本地地图调用失败！'
					});
				}
			});
		}
	},
	onPullDownRefresh() {
		uni.request({
			url: 'http://148.70.148.17:8080/sos/sosinfor',
			// url: 'http://192.168.191.1:8080/user/infor',
			success: (res) => {
				this.students = res.data;
				uni.stopPullDownRefresh();
			},
			fail: (res) => {
				uni.showToast({
					icon: 'none',
					title: '网络服务异常！'
				});
				uni.stopPullDownRefresh();
			}
		});
	}
}
